import React from 'react';
import CKEditor from '@ckeditor/ckeditor5-react';
import BallonBlockEditor from '@ckeditor/ckeditor5-build-balloon-block';
import InsertImagePlugin from './InsertImagePlugin';

const editorConfig = {
  placeholder: 'Type the content here!',
  // plugins: [InsertImagePlugin],
  blockToolbar: [
    'heading',
    '|',
    'bulletedList',
    'numberedList',
    '|',
    'indent',
    'outdent',
    '|',
    'insertImage',
    'link',
    'blockQuote',
    'insertTable',
    'mediaEmbed',
    '|',
    'undo',
    'redo',
  ],
  toolbar: {
    items: [
      'heading',
      '|',
      'bold',
      'italic',
      '|',
      'bulletedList',
      'numberedList',
      '|',
      'indent',
      'outdent',
      '|',
      'link',
      'blockQuote',
    ],
  },
  image: {
    toolbar: [
      'imageStyle:full',
      'imageStyle:side',
      '|',
      'imageTextAlternative',
    ],
  },
  table: {
    contentToolbar: ['tableColumn', 'tableRow', 'mergeTableCells'],
  },
  heading: {
    options: [
      { model: 'paragraph', title: 'Paragraph', class: 'ck-heading_paragraph' },
      {
        model: 'heading2',
        view: 'h2',
        title: 'Heading 2',
        class: 'ck-heading_heading2',
      },
      {
        model: 'heading3',
        view: 'h2',
        title: 'Heading 3',
        class: 'ck-heading_heading3',
      },
      {
        model: 'heading4',
        view: 'h4',
        title: 'Heading 4',
        class: 'ck-heading_heading4',
      },
    ],
  },
};

class CKEditorWidget extends React.Component {
  render() {
    const {
      forID,
      classNameWrapper,
      setActiveStyle,
      setInactiveStyle,
      value,
      onChange,
    } = this.props;
    return (
      <div
        className={classNameWrapper}
        style={{
          paddingLeft: 10,
          paddingRight: 10,
          minHeight: 200,
        }}
      >
        <CKEditor
          id={forID}
          editor={BallonBlockEditor}
          data={value}
          config={editorConfig}
          onInit={(editor) => {
            // You can store the "editor" and use when it is needed.
            console.log('Editor is ready to use!', editor);
          }}
          onChange={(event, editor) => {
            const data = editor.getData();
            onChange(data);
            console.log({ event, editor, data });
          }}
          onBlur={() => setInactiveStyle()}
          onFocus={() => setActiveStyle()}
          style={{ outline: 'none' }}
        />
      </div>
    );
  }
}

export default CKEditorWidget;
